import Vue from 'vue'
import axios from 'axios'
import _path from '../assets/js/path.js'
import qs from 'qs';
import store from '../store/index.js'
const ax = {
    //
    getUserInfo(){
        let _data = {
            token: store.getters.token,
            sysNo: 'CSE006',
        }
        console.log('getUserInfo')
        return axios({
            method: 'post',
            //url: 'http://10.23.179.67:8000/get',
            url: _path.account,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: qs.stringify(_data)
        })
    },
    // 
    getRiskData() {
        console.log('getRiskData')
        let riskData = {
            sysNo: 'CSE006',
            paperType: 1,
            paperVersion: 2,  // 20170629  V2
            token: store.getters.token
        }
        return axios({
            method: 'post',
            url: _path.getUrl('RiskLeve'),
            data: qs.stringify(riskData)
        })
    },
    //1000  pdCode
    getFundQuote(data) {
        console.log('getFundQuote');
        console.log(this.$store.state.fundType)
        console.log(this.$store.state.newFundType)
        return axios({
            method: 'post',
            url: _path.getUrl(1000),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: qs.stringify({pdCode: 1001})
            // data: qs.stringify({
            //   pdCode: 1001, //
            // })
        })
    },
    //1001  
    getFundList(){
        // console.log('store.getters.hasGetFundList')
        // console.log(store.getters.hasGetFundList)
        // if(store.getters.hasGetFundList){
        console.log('getFundList');
        return axios({
            method: 'post',
            url: _path.getUrl(1001),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            }
        })
        // }
    },
    //1005 token
    getPaymentData(bizType) {
        let _data = {token: store.getters.token}
        if (!!bizType) {
            _data['bizType'] = bizType
        }
        console.log(_data)
        console.log('getPaymentData');
        return axios({
            method: 'post',
            url: _path.getUrl(1005),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: qs.stringify(_data)
            // data: qs.stringify({
            //   token: context.getters.token
            // })
        })
    },
    //1013    //
    getBankList() {
        let _data = {payChannel: store.state.exists.payChannel}
        console.log('getBankList');
        return axios({
            method: 'post',
            url: _path.getUrl(1013),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            // url: 'http://27.223.31.46:7771/api/v1/finac/bank/list',
            //data:context.state.test02
            data: qs.stringify(_data)
        })
    },
    //1014    /trade/position.json
    getPositionData() {
        let _data = {token: store.getters.token}
        console.log('getPositionData');
        return axios({
            method: 'post',
            url: _path.getUrl(1014),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: qs.stringify(_data)
            // data: qs.stringify({
            //   token: context.getters.token
            // })
        })
    },
    //1015    /trade/flow.json
    getFlowData(data) {
        console.log('getFlowData');
        return axios({
            method: 'post',
            url: _path.getUrl(1015),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: qs.stringify(data)
        })
    },
    //1016 
    getIncomeData(data) {
        console.log('getIncomeData');
        return axios({
            method: 'post',
            url: _path.getUrl(1016),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            //data : data
            data: qs.stringify(data)
        })
    },
    //exists token

    getAccountExists() {
        //if(!store.getters.hasGetUserInfo){
        let _data = {token: store.getters.token}
        console.log('getAccountExists')
        return axios({
            method: 'post',
            url: _path.getUrl(1017),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: qs.stringify(_data)
            // data: qs.stringify({
            //   token: context.getters.token
            // })
        })
        //}
    },
    //1002
    bindCardApply(data) {
        data.token = store.getters.token
        return axios({
            method: 'post',
            url: _path.getUrl(1002),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: qs.stringify(data)
        })
    },
    //codetoken
    getCode(data){
        return axios({
            method: 'get',
            url: _path.code,
            withCredentials: true,
            headers: {
                // 'Access-Control-Allow-Headers':'Content-Type, Authorization',
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            params: {
                code: data
            },
        })
    },
}

export default ax


